.App {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  max-width: 1160px;
  margin: 0 auto;
}

.App-header {
  height: 120px;
  position: relative;
  padding: 20px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

/* EXTERNAL LINKS */

.externalLinks {
  position: absolute;
  right: 0;
  top: 5px;
}

.externalLinks > a {
  margin: 0 20px;
}

.externalLinks > a > img {
  width: 35px;
  -webkit-transition: opacity .5s linear;
  -o-transition: opacity .5s linear;
  transition: opacity .5s linear;
}

.externalLinks > a > img:hover {
  opacity: .3;
}

/* NAVIGATION */

.navigation {
  margin: 0 25px;
}


